
#body1 {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 100vh;                                                          
    background-color: #191a1a;
}


#conpr{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

  
.mcon {
  position: relative;    
  background-color: #242525;    
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
  border-radius: 50px;
  width: 750px;
  height : 105px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Lucida Console", "Courier New", monospace;
  color : white;
  font-size: 36px;
  margin-top: 40px;
  cursor : default;
}

/* for the line animation when hovering */

.text1{
  font-family: "Lucida Console", "Courier New", monospace;
  color : white;
  font-size: 36px;
  cursor: pointer;
}

.text1 .line {
  position: absolute;
  width: 0;
  height: 3px;
  bottom: 40px; 
  left: 25%; 
  background-color: white;
  transition: width 0.5s ease; 
}

.text1:hover .line {
  width: 50%; 
}

#ceva .text1 .line {
  position: absolute;
  width: 0;
  height: 3px;
  bottom: 40px; 
  left: 15%; 
  background-color: white;
  transition: width 0.5s ease; 
}

#ceva .text1:hover .line {
  width: 70%; 
}
  
  
  
