#body1 {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #191a1a;
}

#containerpr {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#containmid {
  background-color: #242525;
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  width: 1300px;
  height: 750px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  font-family: "Lucida Console", "Courier New", monospace;
  color: white;
  font-size: 25px;
  box-sizing: border-box;
}

.profile-photo {
  display: flex;
  justify-content: center;
  margin-bottom: 75px;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 10px 0;
  cursor : default;
  margin-bottom: 22px;
}

.info-label, .info-action {
  padding: 0 20px;
}

.info-label {
  flex: 1;
  text-align: left;
  color: rgb(255, 102, 0);
}

.info-action {
  flex: 1;
  text-align: right;
  color: white;
  position: relative;
  font-size: 20px;
  cursor : pointer;
}

.specialo .linee {
  position: absolute;
  width: 0;
  height: 0.4px;
  bottom: -5px; 
  left: 65%; 
  background-color: white;
  transition: width 0.5s ease;
}

.specialo:hover .linee {
  width: 35%;
}
