
#maindiv
{
    background-color: #191a1a;
    height : 100vh;

    display : flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;

    color : white;
    font-size: 25px;
}

#midcontainerrr {
    background-color: #242525;    
    padding: 20px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    width: 900px;
    height : 355px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


#spanu
{
    color : rgb(255, 102, 0);
}


#div20
{
    cursor : default;
}

 
/* for the home button */

#homediv
{
  position: absolute;
  top : 25px;
  left : 12px;
  font-family: "Lucida Console", "Courier New", monospace;
  height: 75px;
  width: 150px;
}


#spanx1
{
  color :rgb(255, 102, 0);
  font-size : 30px;
}


#spanx2
{
  color : white;
  font-size : 30px;
}

#Q
{
  cursor : default !important;
  margin-bottom: 35px !important;
}


.varspan {
  margin-bottom: 15px; 
  padding: 2px 5px; /* Set default padding */
  box-sizing: border-box; 
  transition: background-color 0.3s ease; 
  cursor: pointer;
  padding-top: 15px;
  padding-bottom: 15px;
}

.varspan:hover {
  background-color: rgba(128, 128, 128, 0.2); 
}

